import { FC } from 'react'
import styled from 'styled-components'
import { Trans } from 'react-i18next'
import tw from 'twin.macro'
import loadable from '@loadable/component'

import { FooterWrapper } from './common'

const Wrapper = styled(FooterWrapper)`
  ${tw`mb-0`}
`

const LanguageSwitch = loadable(() => import('./language-switch'), {
  ssr: false,
})

const nowYear = new Date().getFullYear()

/* eslint-disable prettier/prettier */
export const Meta: FC<Record<string, never>> = () => (
  <Wrapper>
      <LanguageSwitch />
    <div>
      <Trans i18nKey="ui:meta_first_line">
         <a href="https://reactjs.org">React.js</a>  <a href="https://gatsbyjs.org">Gatsby.js</a>  |  Netlify | <a href="/rss.xml">RSS </a>
      </Trans>
    </div>
    <div>
      <Trans i18nKey="ui:meta_second_line">
         <a href="https://creativecommons.org/licenses/by-sa/4.0/legalcode">CC-BY-SA 4.0</a>  |  <a href="https://github.com/KagamiChan/kurihara/issues/new">  issue </a>
      </Trans>
    </div>
    <div>2013-{nowYear}    / </div>
  </Wrapper>
)
/* eslint-enable prettier/prettier */
